* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  width: 100%;
  height: 200px;
  display: block;
}

.gallery {
  flex-wrap: wrap;
  gap: 20px;
  display: flex;
}

.photo-card {
  width: calc(25% - 15px);
  border-radius: 4px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, .14), 0 1px 3px rgba(0, 0, 0, .12), 0 2px 1px rgba(0, 0, 0, .2);
}

.info {
  justify-content: space-around;
  font-size: 14px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  display: flex;
}

button {
  margin-top: 50px;
}

button:disabled {
  cursor: not-allowed;
  background-color: #c1c1c1;
}

button.hidden {
  display: none;
}

input, button {
  border-radius: 15px;
  padding: 10px 20px;
  font-size: 20px;
}

input[type="submit"], button {
  color: #fff;
  cursor: pointer;
  background-color: rgba(45, 45, 233, .73);
  border: none;
}

